@import "~antd/dist/antd.css";

.ant-input-number,
.ant-picker {
  width: 100%;
}

.row-dragging {
  background: #fafafa;
  border: 1px solid #ccc;
}

.row-dragging td {
  padding: 16px;
}

.row-dragging .drag-visible {
  visibility: visible;
}

.scroll-message {
  text-align: center;
  margin-top: 16px;
}
